import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
const PoliticallyExposed = (props) => {
    const taxStatus = props.location.state?.taxStatus

    const [isTaxResident, setIsTaxResident] = React.useState();
    const [politicalExposure, setPoliticalExposure] = React.useState('N');

    const isTaxResidentOptions = [
        {
            key: 'YES',
            data: 'Yes'
        },
        {
            key: 'NO',
            data: 'No'
        }
    ]
    const politicalExposureOptions = [
        {
            key: 'Y',
            data: 'Yes'
        },
        {
            key: 'N',
            data: 'No'
        },
        {
            key: 'R',
            data: 'No, but I am related to politically exposed person'
        }
    ];

    React.useEffect(() => {
        if(!props.location.state?.taxStatus){
			props.history.push('/activate-direct-mf/tax-status')
		} else {
            db.get(db.model.userData).then((u) => {
                if (u) {
                    setIsTaxResident(u.isTaxResident || taxStatus === '2' ? 'YES' : 'NO');
                    setPoliticalExposure(u.politicalExposure || 'N');
                }
            })
        }
    }, []);

    return (
        <div className='mainContainer'>
            <MainHeader
                header="Activate mutual funds"
                navigation={props.history}
            />
            <ProgressBar now={65} className="progress-bar-custom" />
            <div className="dataContainer" style={{ paddingTop: '42px' }}>
                <div className="header1dark">Are you a politically exposed person?</div>
                {politicalExposureOptions.map((obj) => (
                    <div
                        key={obj.key}
                        style={{
                            display: 'flex',
                            marginTop: "6%"
                        }}>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="politicalExposure"
                                value={obj.key}
                                onChange={(val) => {
                                    setPoliticalExposure(val.target.value)
                                }}
                                checked={politicalExposure === obj.key}
                            />
                            <label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.data}</label>
                        </div>
                    </div>
                ))}

                <div className="header1dark" style={{marginTop:"64px"}}>Are you a tax resident of any other country than India?</div>
                {isTaxResidentOptions.map((obj) => (
                    <div
                        key={obj.key}
                        style={{
                            display: 'flex',
                            marginTop: "6%"
                        }}>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="resident"
                                value={obj.key}
                                onChange={(val) => {
                                    console.log(val.target.value)
                                    setIsTaxResident(val.target.value)
                                }}
                                checked={isTaxResident === obj.key}
                            />
                            <label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.data}</label>
                        </div>
                    </div>
                ))}
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    navigation={props.history}
                    buttonText="Confirm"
                    next={taxStatus === '1' ? "/activate-direct-mf/bank-details" : "/activate-direct-mf/nominee"}
                    params={props.location.state}
                    saveData={{
                        isTaxResident,
                        politicalExposure
                    }} />
            </div>
        </div>
    )
}

export default PoliticallyExposed
